.main {
  height: 100vh;
  overflow: auto;
  display: flex;
  background-color: #212529;
}

.leftContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
  width: 25%;
  height: 100%;
  background-color: #1e1e2f;
  font-family: sans-serif;
  border-radius: 10px;
  padding: 20px;
}

.rightContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
  width: 75%;
  height: 100%;
  background-color: #1e1e2f;
  font-family: sans-serif;
  border-radius: 10px;
  padding: 20px 20px 20px 0;
}

.date-panel {
  background-color: white;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.dropdown {
  height: 170px;
}

.datatable {
  /* height: 500px; */
  width: 100%;
  background-color: white;
  overflow: auto;
}

.data-table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  border: 1px solid #dddddd;
  padding: 8px;
}

.chart-container {
  width: 100%;
  /* margin: 40px; */
  height: 100%;
  display: flex;
  flex-direction: column;
}

.chart {
  /* margin-top: 30px; */
  height: 100%;
  background-color: white;
}

.react-datepicker__input-container > input {
  width: 90px !important;
}

.chart-title {
  font-size: large;
  padding: 10px;
}

@media only screen and (max-width: 780px) {
  .leftContent {
    height: 30%;
    width: 100%;
  }

  .rightContent {
    height: 70%;
    width: 100%;
    padding-top: 0;
    padding-left: 20px;
  }

  .main {
    display: block;
  }
}
